.navMaster {
    position: fixed;
    max-width: 100%;
    width: 100%;
    z-index: 5;
}
.navbar {
    background-color: black;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: fixed;
    top: 0;
    z-index: 10;
}

.nav-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80px;
    max-width: 85%;
}
@media (max-width: 1024px) {
    .nav-container {
        max-width: 90%;
    }
    .navbar {
        opacity: 1;
    }
}
.nav-logo {
    align-items: center;
    margin-left: 10px;
    cursor: pointer;
    flex-grow: 1;
    width: 180px;
}
.nav-logo2 {
    align-items: center;
    cursor: pointer;
    flex-grow: 1;
    width: 60px;
    margin-left: -4rem;
}
@media (min-width: 1024px) and (max-width: 1350px) {
    .nav-logo {
        width: 130px;
        height: 50px;
    }
    .nav-logo2 {
        width: 50px;
        margin-left: -5rem;
    }
}
@media (min-width: 1350px) and (max-width: 1500px) {
    .nav-logo2 {
        margin-left: -6rem;
    }
}
@media (min-width: 1024px) and (max-width: 1100px) {
    .nav-logo2 {
        margin-left: -6rem;
    }
}

@media (min-width: 760px) and (max-width: 1024px) {
    .nav-logo {
        margin-left: 10px;
        width: 150px;
        height: 50px;
        margin-top: -10px;
    }
    .nav-logo2 {
        width: 50px;
        margin-left: 0px;
        margin-top: -5px;
    }
    .navbar {
        height: 70px;
    }
}
@media (max-width: 760px) {
    .nav-logo {
        width: 90px;
        height: 30px;
        margin-top: -15px;
    }
    .nav-logo2 {
        width: 50px;
        margin-top: -12px;
        margin-left: 0px;
    }
    .navbar {
        height: 70px;
    }
}

@media (max-width: 540px) {
    .nav-logo {
        margin-top: -18px;
        margin-left: 2px;
    }
    .nav-logo2 {
        display: none;
    }
    .navbar {
        height: 50px;
    }
}

.nav-menu {
    display: flex;
    list-style: none;
    text-align: center;
    margin-right: 2rem;
    margin-top: 3rem;
    z-index: 4;
}

.nav-links {
    font-family: 'Roboto';
    color: black;
    font-weight: bold;
    text-decoration: none;
    padding: 0.7rem 1rem;
}
.nav-links:hover {
    text-decoration: none;
    color: white;
    cursor: pointer;
}
@media (min-width: 1024px) and (max-width: 1350px) {
    .nav-menu {
        display: flex;
        list-style: none;
        text-align: center;
        margin-top: 3rem;
        z-index: 2;
        margin-left: 1rem;
    }
}
