.nav-item {
    line-height: 40px;
    margin-right: 1rem;
}

.nav-item:after {
    content: '';
    display: block;
    height: 2px;
    width: 0;
    background: transparent;
    transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
    width: 100%;
    background: black;
}

.nav-item .active {
    color: white;
    padding-bottom: 0.8rem;
    border-bottom: 3px solid black;
}

.navBox img {
    position: fixed;
    top: 30px;
    left: 30%;
    width: 70%;
    height: 70px;
    z-index: 2;
}
.menuButton {
    color: #ffc107;
}
.searchIcon {
    z-index: 5;
    position: relative;
    top: 1rem;
    right: 1rem;
    cursor: pointer;
}
@media (min-width: 1024px) and (max-width: 1350px) {
    .searchBar {
        margin-right: -6rem;
        width: 105px;
    }
    .navBox img {
        width: 80%;
        left: 25%;
    }
}

@media (min-width: 541px) and (max-width: 1024px) {
    .searchIcon {
        top: -1rem;
    }
}

@media (max-width: 540px) {
    .searchIcon {
        top: -1.5rem;
    }
    .searchBar {
        width: 100%;
        top: -0.5rem;
    }
}

